@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
.tableHeads {
  position: relative;
  display: inline-block;
  padding: 3px 10px;
  border-radius: 4px;
  font-size: 16px;
  color: #eaf1f2;
}
.arrow-up {
  margin-left: 55px;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 6px solid rgba(228, 203, 203, 1);
  margin-bottom: 5px;
  cursor: pointer;
}
.arrow-down {
  margin-left: 55px;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 6px solid rgba(228, 203, 203, 1);
  cursor: pointer;
  margin-bottom: 0px;
}
.arrow-up:hover {
  border-bottom: 6px solid #fff;
}
.arrow-down:hover {
  border-top: 6px solid #fff;
}
.bootwrap .sorting-up-down {
  position: absolute;
  margin-left: -50px;
  margin-top: -2px;
  width: 80px;
  height: 30px;
  background: rgba(228, 203, 203, 0.3);
  border: none;
  border-radius: 4px;
}
.nameColumn {
  font-weight: bold;
}
.index-column {
  color: #838383;
}
.mass-column {
  min-width: 120px;
}
.table td {
  padding: 15px 10px 15px 22px !important;
}
.tableData tr:hover,
.table-striped tbody tr:nth-of-type(odd):hover {
  background-color: rgba(218, 100, 224, 0.3);
}
.table td,
.table th {
  border-top: none !important;
}

.tableData table th:nth-child(2) {
  position: sticky;
  position: -webkit-sticky;
  z-index: 1;
  left: 0px;
  background: #db66e1ff;
}

.tableData table td:nth-child(2) {
  position: sticky;
  position: -webkit-sticky;
  z-index: 1;
  left: 0px;
  background: white;
  width: 179px;
}

.head-srtipe {
  display: block;
  width: 100%;
  height: 1px;
  background: #fff;
}
.head-for-table {
  background: linear-gradient(90deg, rgba(218, 100, 224, 1) 0%, rgba(229, 113, 113, 1) 100%);
}
.head-for-table h1 {
  padding-top: 30px;
  margin-bottom: -0.55rem !important;
  line-height: 33px;
  color: #fff;
  font-family: 'Abril Fatface', cursive;
  font-size: 44px;
}

.bgColor {
  background: linear-gradient(90deg, rgba(218, 100, 224, 1) 0%, rgba(229, 113, 113, 1) 100%);
}
.bgColorBody {
  background: #fff;
}

.select-form-bg {
  position: relative;
}
.select-form {
  position: absolute;
  margin-top: 62px;
  z-index: 10;
  width: 350px;
}
@media (max-width: 700px) {
  .select-form {
    width: 100%;
  }
  .head-for-table h1 {
    padding-top: 5px;
    padding-left: 20px;
    line-height: 33px;
    color: #fff;
    font-family: 'Abril Fatface', cursive;
    font-size: 34px;
  }
  .select-form {
    position: absolute;
    margin-top: 35px;
    z-index: 10;
    width: 350px;
  }
}

