.tableHeads {
  position: relative;
  display: inline-block;
  padding: 3px 10px;
  border-radius: 4px;
  font-size: 16px;
  color: #eaf1f2;
}
.arrow-up {
  margin-left: 55px;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 6px solid rgba(228, 203, 203, 1);
  margin-bottom: 5px;
  cursor: pointer;
}
.arrow-down {
  margin-left: 55px;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 6px solid rgba(228, 203, 203, 1);
  cursor: pointer;
  margin-bottom: 0px;
}
.arrow-up:hover {
  border-bottom: 6px solid #fff;
}
.arrow-down:hover {
  border-top: 6px solid #fff;
}
.bootwrap .sorting-up-down {
  position: absolute;
  margin-left: -50px;
  margin-top: -2px;
  width: 80px;
  height: 30px;
  background: rgba(228, 203, 203, 0.3);
  border: none;
  border-radius: 4px;
}
.nameColumn {
  font-weight: bold;
}
.index-column {
  color: #838383;
}
.mass-column {
  min-width: 120px;
}
.table td {
  padding: 15px 10px 15px 22px !important;
}
.tableData tr:hover,
.table-striped tbody tr:nth-of-type(odd):hover {
  background-color: rgba(218, 100, 224, 0.3);
}
.table td,
.table th {
  border-top: none !important;
}

.tableData table th:nth-child(2) {
  position: sticky;
  position: -webkit-sticky;
  z-index: 1;
  left: 0px;
  background: #db66e1ff;
}

.tableData table td:nth-child(2) {
  position: sticky;
  position: -webkit-sticky;
  z-index: 1;
  left: 0px;
  background: white;
  width: 179px;
}
